<template>
  <div>
    <h3 align="center"><a-icon type="form" /></h3>
    <a-card>
      <a-row gutter="30">
        <a-form-model ref="carFormRef" :rules="rules" :model="formdata" class="carForm">
          <a-col :span="4">
            <a-form-model-item prop="car_num">
              <a-input v-model="formdata.car_num" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="3">
            <a-form-model-item prop="price">
              <a-input type="number" :min="0" v-model="formdata.price" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="4">
            <a-form-model-item prop="city_name">
              <a-input v-model="formdata.city_name" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="4">
            <a-form-model-item class="carBtn">
              <a-button type="primary" @click="addCar"></a-button>
            </a-form-model-item>
          </a-col>
        </a-form-model>
      </a-row>

      <a-table :rowKey="car_num" :columns="columns" :dataSource="carList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delCar(data.car_num)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: '',
    dataIndex: 'car_num',
    width: '30%',
    align: 'center',
    key: 'car_num'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '20%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'city_name',
    width: '30%',
    align: 'center',
    key: 'city_name'
  },
  {
    title: '',
    width: '20%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      formdata: {
        car_num: '',
        price: '',
        city_name: ''
      },
      rules: {
        car_num: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        city_name: [
          { required: true, message: '!', trigger: 'blur' }
        ]
      },
      carList: [],
      columns
    }
  },
  created () {
    this.getCarList()
  },
  methods: {
    async getCarList () {
      const { data: res } = await this.$http.get('cars/list')
      if (res.status !== 200) return this.$message.error(res.message)
      this.carList = res.data
    },
    async addCar () {
      this.$refs.carFormRef.validate(async valid => {
        this.$confirm({
          title: '',
          content: '?',
          onOk: async () => {
            console.log(this.formdata)
            if (!valid) return this.$message.error('!')
            const { data: res } = await this.$http.post('car/addCar', {
              car_num: this.formdata.car_num,
              price: this.formdata.price - 0,
              city_name: this.formdata.city_name
            })
            if (res.status !== 200) return this.$message.error(res.message)
            this.$message.success('!')
            this.getCarList()
          },
          onCancel: () => {
            this.$message.info('!')
          }
        })
      })
    },
    async delCar (num) {
      this.$confirm({
        title: '?(:' + `${num}` + ')',
        content: '',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`car/${num}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('!')
          this.getCarList()
        },
        onCancel: () => {
          this.$message.info('!')
        }
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
