<template>
  <div>
    <h3 align="center"><a-icon type="form" /></h3>
    <a-card>
      <a-row gutter="30">
        <a-form-model ref="resvFormRef" :rules="rules" :model="formdata" class="resvForm">
          <a-col :span="4">
            <a-form-model-item label="" required prop="date">
              <a-date-picker
                v-model="formdata.date"
                show-time
                type="date"
                placeholder="Pick a date"
                style="width: 100%;"
              />
            </a-form-model-item>
          </a-col>
        </a-form-model>
      </a-row>
      <a-table :rowKey="flight_num" :columns="columns" :dataSource="flightList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" @click="addFlightResv(data.flight_num)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: '',
    dataIndex: 'flight_num',
    width: '15%',
    align: 'center',
    key: 'flight_num'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '12%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'num_seats',
    width: '12%',
    align: 'center',
    key: 'num_seats'
  },
  {
    title: '',
    dataIndex: 'from_city',
    width: '20%',
    align: 'center',
    key: 'from_city'
  },
  {
    title: '',
    dataIndex: 'ariv_city',
    width: '20%',
    align: 'center',
    key: 'ariv_city'
  },
  {
    title: '',
    width: '20%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      formdata: {
        date: undefined
      },
      rules: {
        date: [
          { required: true, message: '!', trigger: 'blur' }
        ]
      },
      flightList: [],
      columns
    }
  },
  created () {
    this.getFlightList()
  },
  methods: {
    async getFlightList () {
      const { data: res } = await this.$http.get('flights/list')
      if (res.status !== 200) return this.$message.error(res.message)
      this.flightList = res.data
    },
    async addFlightResv (num) {
      this.$refs.resvFormRef.validate(async valid => {
        this.$confirm({
          title: '',
          content: '?',
          onOk: async () => {
            console.log(this.formdata)
            if (!valid) return this.$message.error('!')
            const { data: res } = await this.$http.post('reservation/addReservation', {
              resv_key: num,
              cust_name: window.sessionStorage.getItem('cust_name'),
              type: 2,
              res_date: this.formdata.date
            })
            if (res.status !== 200) return this.$message.error(res.message)
            this.$message.success('!')
            this.getFlightList()
          },
          onCancel: () => {
            this.$message.info('!')
          }
        })
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
