<template>
  <div>
    <h3 align="center"><a-icon type="ordered-list" /></h3>
    <a-card>
      <template>
        <a-list bordered :data-source="routeList">
          <a-list-item slot="renderItem" slot-scope="item">
            {{ item }}
          </a-list-item>
          <div slot="header" align="center">
            
          </div>
        </a-list>
      </template>
    </a-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      routeList: []
    }
  },
  created () {
    this.getRouteList()
  },
  methods: {
    async getRouteList () {
      const { data: res } = await this.$http.get('reservations/routeList?cust_name=' + window.sessionStorage.getItem('cust_name'))
      if (res.status !== 200) return this.$message.error(res.message)
      this.routeList = res.data
    }
  }
}
</script>
