import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login'
import Admin from '../views/Admin'

// 
import Index from '../components/admin/Index'
import CarList from '../components/Car/CarList'
import FlightList from '../components/Flight/FlightList'
import HotelList from '../components/Hotel/HotelList'
import ResvList from '../components/Resv/ResvList'
import MyResv from '../components/Customer/MyResv'
import MyRoute from '../components/Customer/MyRoute'
import FlightManager from '../components/Flight/FlightManager'
import CarManager from '../components/Car/CarManager'
import HotelManager from '../components/Hotel/HotelManager'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/',
    name: 'admin',
    component: Admin,
    children: [
      { path: '/index', component: Index },
      { path: '/carList', component: CarList },
      { path: '/flightList', component: FlightList },
      { path: '/hotelList', component: HotelList },
      { path: '/resvList', component: ResvList },
      { path: '/myResv', component: MyResv },
      { path: '/myRoute', component: MyRoute },
      { path: '/flightManager', component: FlightManager },
      { path: '/carManager', component: CarManager },
      { path: '/hotelManager', component: HotelManager }
    ]
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  const token = window.sessionStorage.getItem('token')
  if (to.path === '/login') return next()
  if (!token && to.path === '/admin') {
    next('/login')
  } else {
    next()
  }
})

export default router
