<template>
  <div>
    <h3 align="center"><a-icon type="unordered-list" /></h3>
    <a-card>
      <a-row><span><a-icon type="car" /></span></a-row>
      <a-table :rowKey="resv_key" :columns="cars" :dataSource="carResvList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delResv(data.resv_key)"></a-button>
          </div>
        </template>
      </a-table>
      <a-row><span><a-icon type="rocket" /></span></a-row>
      <a-table :rowKey="resv_key" :columns="flights" :dataSource="flightResvList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delResv(data.resv_key)"></a-button>
          </div>
        </template>
      </a-table>
      <a-row><span><a-icon type="home" /></span></a-row>
      <a-table :rowKey="resv_key" :columns="hotels" :dataSource="hotelResvList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delResv(data.resv_key)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const cars = [
  {
    title: '',
    dataIndex: 'resv_key',
    width: '15%',
    align: 'center',
    key: 'resv_key'
  },
  {
    title: '',
    dataIndex: 'cust_name',
    width: '20%',
    align: 'center',
    key: 'cust_name'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '15%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'from_city',
    width: '20%',
    align: 'center',
    key: 'from_city'
  },
  {
    title: '',
    dataIndex: 'res_date',
    width: '15%',
    align: 'center',
    key: 'res_date'
  },
  {
    title: '',
    width: '15%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
const flights = [
  {
    title: '',
    dataIndex: 'resv_key',
    width: '15%',
    align: 'center',
    key: 'resv_key'
  },
  {
    title: '',
    dataIndex: 'cust_name',
    width: '15%',
    align: 'center',
    key: 'cust_name'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '10%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'from_city',
    width: '15%',
    align: 'center',
    key: 'from_city'
  },
  {
    title: '',
    dataIndex: 'ariv_city',
    width: '15%',
    align: 'center',
    key: 'ariv_city'
  },
  {
    title: '',
    dataIndex: 'res_date',
    width: '15%',
    align: 'center',
    key: 'res_date'
  },
  {
    title: '',
    width: '15%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
const hotels = [
  {
    title: '',
    dataIndex: 'resv_key',
    width: '15%',
    align: 'center',
    key: 'resv_key'
  },
  {
    title: '',
    dataIndex: 'cust_name',
    width: '20%',
    align: 'center',
    key: 'cust_name'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '15%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'from_city',
    width: '20%',
    align: 'center',
    key: 'from_city'
  },
  {
    title: '',
    dataIndex: 'res_date',
    width: '15%',
    align: 'center',
    key: 'res_date'
  },
  {
    title: '',
    width: '15%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      carResvList: [],
      flightResvList: [],
      hotelResvList: [],
      cars,
      hotels,
      flights
    }
  },
  created () {
    this.getCarResvList()
    this.getFlightResvList()
    this.getHotelResvList()
  },
  methods: {
    async getCarResvList () {
      const { data: res } = await this.$http.get('reservations/carList?cust_name=' + window.sessionStorage.getItem('cust_name'))
      if (res.status !== 200) return this.$message.error(res.message)
      this.carResvList = res.data
      this.carResvList.city_name = res.data.city_name
    },
    async getFlightResvList () {
      const { data: res } = await this.$http.get('reservations/flightList?cust_name=' + window.sessionStorage.getItem('cust_name'))
      if (res.status !== 200) return this.$message.error(res.message)
      this.flightResvList = res.data
    },
    async getHotelResvList () {
      const { data: res } = await this.$http.get('reservations/hotelList?cust_name=' + window.sessionStorage.getItem('cust_name'))
      if (res.status !== 200) return this.$message.error(res.message)
      this.hotelResvList = res.data
    },
    async delResv (num) {
      this.$confirm({
        title: '?(:' + `${num}` + ')',
        content: '',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`reservation/${num}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('!')
          this.getCarResvList()
          this.getFlightResvList()
          this.getHotelResvList()
        },
        onCancel: () => {
          this.$message.info('!')
        }
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
