<template>
  <div class="container">
    <div class="loginBox">
      <h2 align="center" class="title"></h2>
      <a-form-model ref="loginFormRef" :rules="rules" :model="formdata" class="loginForm">
        <a-form-model-item prop="cust_name">
          <a-input v-model="formdata.cust_name" placeholder="...">
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)"/>
          </a-input>
        </a-form-model-item>

        <a-form-model-item prop="password">
          <a-input v-model="formdata.password" placeholder="..." type="password" v-on:keyup.enter="login">
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
          </a-input>
        </a-form-model-item>

        <a-form-model-item class="loginBtn">
          <a-button type="primary" style="margin:10px;" @click="login"></a-button>
          <a-button type="info" style="margin:10px;" @click="sign"></a-button>
        </a-form-model-item>
      </a-form-model>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      formdata: {
        username: '',
        password: ''
      },
      rules: {
        cust_name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 4, max: 12, message: '412!', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 6, max: 20, message: '620!', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return this.$message.error('!')
        const { data: res } = await this.$http.post('login', this.formdata)
        if (res.status !== 200) return this.$message.error(res.message)
        window.sessionStorage.setItem('token', res.token)
        window.sessionStorage.setItem('cust_name', res.custName)
        this.$router.push('/index')
      })
    },
    sign () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return this.$message.error('!')
        const { data: res } = await this.$http.post('sign', this.formdata)
        if (res.status !== 200) return this.$message.error(res.message)
        return this.$message.success('!')
      })
    }
  }
}
</script>

<style scoped>
.container {
  height: 100%;
  background-color: rgb(224, 192, 160);;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loginBox {
  width: 450px;
  height: 300px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 9px;
}

.loginForm {
  width: 100%;
  position: absolute;
  bottom: 10%;
  padding: 0 20px;
  box-sizing: border-box;
}

.loginBtn {
  display: flex;
  justify-content: flex-end;
}

.title {
  margin-top: 16px;
  font-family: ;
}
</style>
