<template>
  <div>
    <h3 align="center"><a-icon type="form" /></h3>
    <a-card>
      <a-row gutter="30">
        <a-form-model ref="flightFormRef" :rules="rules" :model="formdata" class="flightForm">
        <a-col :span="4">
          <a-form-model-item prop="flight_num">
            <a-input v-model="formdata.flight_num" placeholder=""></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="3">
          <a-form-model-item prop="price">
            <a-input type="number" :min="0" v-model="formdata.price" placeholder=""></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="3">
          <a-form-model-item prop="num_seats">
            <a-input type="number" :min="0" v-model="formdata.num_seats" placeholder=""></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="4">
          <a-form-model-item prop="from_city">
            <a-input v-model="formdata.from_city" placeholder=""></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="4">
          <a-form-model-item prop="ariv_city">
            <a-input v-model="formdata.ariv_city" placeholder=""></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="4">
          <a-form-model-item class="flightBtn">
            <a-button type="primary" @click="addFlight"></a-button>
          </a-form-model-item>
        </a-col>
        </a-form-model>
      </a-row>

      <a-table :rowKey="flight_num" :columns="columns" :dataSource="flightList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delFlight(data.flight_num)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: '',
    dataIndex: 'flight_num',
    width: '15%',
    align: 'center',
    key: 'flight_num'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '12%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'num_seats',
    width: '12%',
    align: 'center',
    key: 'num_seats'
  },
  {
    title: '',
    dataIndex: 'from_city',
    width: '20%',
    align: 'center',
    key: 'from_city'
  },
  {
    title: '',
    dataIndex: 'ariv_city',
    width: '20%',
    align: 'center',
    key: 'ariv_city'
  },
  {
    title: '',
    width: '20%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      formdata: {
        flight_num: '',
        price: '',
        num_seats: '',
        from_city: '',
        ariv_city: ''
      },
      rules: {
        flight_num: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        num_seats: [
          { required: true, message: '!()', trigger: 'blur' }
        ],
        from_city: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        ariv_city: [
          { required: true, message: '!', trigger: 'blur' }
        ]
      },
      flightList: [],
      columns
    }
  },
  created () {
    this.getFlightList()
  },
  methods: {
    async getFlightList () {
      const { data: res } = await this.$http.get('flights/list')
      if (res.status !== 200) return this.$message.error(res.message)
      this.flightList = res.data
    },
    async addFlight () {
      this.$refs.flightFormRef.validate(async valid => {
        this.$confirm({
          title: '',
          content: '?',
          onOk: async () => {
            console.log(this.formdata)
            if (!valid) return this.$message.error('!')
            const { data: res } = await this.$http.post('flight/addFlight', {
              flight_num: this.formdata.flight_num,
              price: this.formdata.price - 0,
              num_seats: this.formdata.num_seats - 0,
              from_city: this.formdata.from_city,
              ariv_city: this.formdata.ariv_city
            })
            if (res.status !== 200) return this.$message.error(res.message)
            this.$message.success('!')
            this.getFlightList()
          },
          onCancel: () => {
            this.$message.info('!')
          }
        })
      })
    },
    async delFlight (num) {
      this.$confirm({
        title: '?(:' + `${num}` + ')',
        content: '',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`flight/${num}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('!')
          this.getFlightList()
        },
        onCancel: () => {
          this.$message.info('!')
        }
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
