<template>
  <div>
    <h3 align="center"><a-icon type="form" /></h3>
    <a-card>
      <a-row gutter="30">
        <a-form-model ref="hotelFormRef" :rules="rules" :model="formdata" class="hotelForm">
          <a-col :span="4">
            <a-form-model-item prop="hotel_name">
              <a-input v-model="formdata.hotel_name" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="3">
            <a-form-model-item prop="price">
              <a-input type="number" :min="0" v-model="formdata.price" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="3">
            <a-form-model-item prop="num_rooms">
              <a-input type="number" :min="0" v-model="formdata.num_rooms" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="4">
            <a-form-model-item prop="city_name">
              <a-input v-model="formdata.city_name" placeholder=""></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="4">
            <a-form-model-item class="hotelBtn">
              <a-button type="primary" @click="addHotel"></a-button>
            </a-form-model-item>
          </a-col>
        </a-form-model>
      </a-row>

      <a-table :rowKey="hotel_name" :columns="columns" :dataSource="hotelList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delHotel(data.hotel_name)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: '',
    dataIndex: 'hotel_name',
    width: '15%',
    align: 'center',
    key: 'hotel_name'
  },
  {
    title: '',
    dataIndex: 'price',
    width: '15%',
    align: 'center',
    key: 'price'
  },
  {
    title: '',
    dataIndex: 'num_rooms',
    width: '15%',
    align: 'center',
    key: 'num_rooms'
  },
  {
    title: '',
    dataIndex: 'city_name',
    width: '20%',
    align: 'center',
    key: 'city_name'
  },
  {
    title: '',
    width: '20%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      formdata: {
        hotel_name: '',
        price: '',
        num_rooms: '',
        city_name: ''
      },
      rules: {
        hotel_name: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        num_rooms: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        city_name: [
          { required: true, message: '!', trigger: 'blur' }
        ]
      },
      hotelList: [],
      columns
    }
  },
  created () {
    this.getHotelList()
  },
  methods: {
    async getHotelList () {
      const { data: res } = await this.$http.get('hotels/list')
      if (res.status !== 200) return this.$message.error(res.message)
      this.hotelList = res.data
    },
    async addHotel () {
      this.$refs.hotelFormRef.validate(async valid => {
        this.$confirm({
          title: '',
          content: '?',
          onOk: async () => {
            console.log(this.formdata)
            if (!valid) return this.$message.error('!')
            const { data: res } = await this.$http.post('hotel/addHotel', {
              hotel_name: this.formdata.hotel_name,
              price: this.formdata.price - 0,
              num_rooms: this.formdata.num_rooms - 0,
              city_name: this.formdata.city_name
            })
            if (res.status !== 200) return this.$message.error(res.message)
            this.$message.success('!')
            this.getHotelList()
          },
          onCancel: () => {
            this.$message.info('!')
          }
        })
      })
    },
    async delHotel (num) {
      this.$confirm({
        title: '?(:' + `${num}` + ')',
        content: '',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`hotel/${num}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('!')
          this.getHotelList()
        },
        onCancel: () => {
          this.$message.info('!')
        }
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
